// utils
import {
  createIsAllMethod,
  createIsNotMethod,
  createIsAnyMethod
} from './_internal/functionUtils';

const isString = (object) => {
  return typeof object === 'string';
};

export const isAllString = createIsAllMethod(isString);
export const isNotString = createIsNotMethod(isString);
export const isAnyString = createIsAnyMethod(isString);

export default isString;



