// utils
import {
  createIsAllMethod,
  createIsNotMethod,
  createIsAnyMethod
} from './_internal/functionUtils';

const isObject = (object) => {
  return typeof object === 'object';
};

export const isAllObject = createIsAllMethod(isObject);
export const isNotObject = createIsNotMethod(isObject);
export const isAnyObject = createIsAnyMethod(isObject);

export default isObject;



