// utils
import {
  createIsAllMethod,
  createIsNotMethod,
  createIsAnyMethod
} from './_internal/functionUtils';

const isRegExp = (object) => {
  return object instanceof RegExp;
};

export const isAllRegExp = createIsAllMethod(isRegExp);
export const isNotRegExp = createIsNotMethod(isRegExp);
export const isAnyRegExp = createIsAnyMethod(isRegExp);

export default isRegExp;



